/* StratBox.css */

.strat-box {
  background-color: rgba(128, 128, 128, 0.1); 
  color: black;
  padding: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
  width: 100%;
  box-sizing: border-box; 
  min-height: 50px; /* Ensures that each box has a minimum height */
}

.strat-box.selected {
  background-color: #c5c5c5;
  color: #000;
}