/* IdentifyWeaknessesDisplay.css */

.identify-weaknesses-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  bottom: 20px;
  height: 100%; 
  padding: 20px;
  overflow-y: auto;
  color: black;
  box-sizing: border-box;
}


.update-narrative {
  margin-top: 20px;
  padding: 10px 20px;
  position: absolute;
  bottom: 20px;
  cursor: pointer;
  background-color: #007bff; /* Example button color */
  color: white; /* Button text color */
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s;
  /* Additional styling for hover state */
  &:hover {
    background-color: #0056b3;
  }

  /* Styling for disabled state */
  &:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
}

.spacer {
  height: 50px; /* Adjust based on the actual height of your timer and counter */
  visibility: hidden;
}