/* NarrativeImpactDisplay.css */

.updated-narrative-impact-display-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  max-height: 450px; 
  overflow-y: auto;
  color: black;
}

.updated-narrative-impact-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  text-align: center; 
  color: black;
}

.continue-button {
  margin-top: 20px;
  padding: 10px 20px;
  cursor: pointer;
  position: absolute;
  bottom: 20px;  
  background-color: #007bff; /* Example button color */
  color: white; /* Button text color */
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s;
  /* Additional styling for hover state */
  &:hover {
    background-color: #0056b3;
  }

  /* Styling for disabled state */
  &:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
}





